<script>
	import request from '../../../../static/js/request'
	import result from '../../../../static/js/result'
	export default {
		data() {
			return {
                studentOrderList :[],//全部交费记录数组
                payListed:[],//已交费记录数组
                unPayList:[],//未支付交费记录数组
                showAll:true,
                showPay:false,
                showUnPay:false,
			}
		},
		mounted() {

		},
		created() {
            this.getUserInfo();
            //this.getInfo();
            console.log(this.payList)
		},
		methods: {
		    goBack(){
		        this.$router.go(-1)
            },
		    goOrder(orderId){
		        this.$router.push('/payRecordDetail?orderId='+orderId)
            },
            async getUserInfo(){
                let params  = {
                    access_token:this.$store.state.token,
                    orderUserId:this.$route.query.studentId
                }
                let loading = weui.loading('loading', {
                    className: 'custom-classname'
                });

               /* let headers = {
                    contentType:'application/x-www-form-urlencoded',
                    token:this.$store.state.token
                }*/
                let getUserInfo = await request('/api/token/order/selectOrderInfoList',
                    'post',params,'application/x-www-form-urlencoded')
                // loading.hide()
                let info = result(getUserInfo)
                 loading.hide()
                if(!info) return;
                //console.log('获取电话号成功信息',getUserInfo)
                console.log(info)
                for(let item of info){
                    if(item.orderUserId == this.$route.query.studentId){
                        this.studentOrderList.push(item)
                    }
                }
                console.log(this.studentOrderList)
            },
            async getInfo(){
                let params = {
                    phone:this.$store.state.phone,
                    access_token:this.$store.state.token
                };
                /*let headers = {
                    contentType:'application/x-www-form-urlencoded',
                    token:this.$store.state.token
                };*/
                console.log(this.$store.state.token);
                let myInfo = await request('/api/token/base/info/select',
                    'post', params, 'application/x-www-form-urlencoded');
                // loading.hide()
                let res = result(myInfo);
                // loading.hide()
                if(!res) return;
                //console.log('个人信息',myInfo);
                if(myInfo){
                    this.getOrderInfo(myInfo.data.id)
                }
            },
            async getOrderInfo(id){
                let params = {
                    id:id,
                    access_token:this.$store.state.token
                };
                console.log(params);
                /*let headers = {
                    contentType:'application/x-www-form-urlencoded',
                    token:this.$store.state.token
                };*/
                //console.log(this.$store.state.token);
                let orderInfo = await request('/api/token/order/info/listMember',
                    'post', params,'application/x-www-form-urlencoded');
                // loading.hide()
                let res = result(orderInfo);
                // loading.hide()
                if(!res) return;
                //console.log('交费记录',orderInfo);
                this.payList = orderInfo.data.reverse()//倒叙循环交费记录,最新记录在最上方;
                for(let item of this.payList){
                    if(item.actualPayment!=null){
                        this.payListed.push(item);
                    }else{
                        this.unPayList.push(item);
                    }
                }
                //console.log('已支付数组',this.payListed)
                //console.log('未支付数组',this.unPayList)
            },
            chaxun(e){
                console.log(e);
                if(e == 'showAll'){
                    this.showAll = true;
                    this.showPay = false;
                    this.showUnPay = false;
                }else if(e == 'showPay'){
                    this.showPay = true;
                    this.showAll = false;
                    this.showUnPay = false
                }else if(e == 'showUnPay'){
                    this.showUnPay = true;
                    this.showAll = false;
                    this.showPay = false;
                }
            }
		}
	}
</script>

